import React, { Component } from 'react';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';

import './App.css';
import Model from './Model'
import LeftDrawer from './leftdrawer'
class App extends Component {
  state = {
    menu: null,
    tree: [],
    headers: {},
    init: false,
    showAppBar: true,
    theme: createMuiTheme({
      palette: {
        primary: {
          light: '#7d6cff',
          main: '#5741ff',
          dark: '#452cff',
          contrastText: '#fff',
        },
        secondary: {
          light: '#88b7ff',
          main: '#5f9eff',
          dark: '#2c7fff',
          contrastText: '#000',
        },
      },
    })
  }
  componentDidMount() {
    window.addEventListener('message', this.handleFrameTasks);
    window.parent.postMessage('componentDidMount', window.origin)
  }
  componentWillUnmount() {
    window.removeEventListener('message', this.handleFrameTasks);
  }
  handleFrameTasks = (e) => {
    switch (e.data.action) {
      case 'theme': return this.setState({ theme: createMuiTheme(e.data.value) })
      case 'headers': return this.setState({ headers: e.data.value })
      case 'showAppBar': return this.setState({ showAppBar: e.data.value })
      case 'init': return this.setState({ init: true })
      default: return
    }
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.state.init) {
      if ((!prevState) || (this.state.init !== prevState.init))
        this.requestTree()
    }
  }
  requestTree = () => {
    const request = new Request('tree', {
      headers: this.state.headers
    })
    fetch(request).then(data => data.json()).then(tree => {
      this.setState({ tree })
    }).catch(err => {
      console.error(err)
    })
  }
  handleItemMenuClick = (menu) => {
    window.parent.postMessage({ action: 'configurationMenuChanged', item: menu }, window.origin)
    this.setState({ menu })
  }
  render() {
    if (!this.state.init) return null
    const content = this.state.menu === null ?
      <div>
        <p>Select an item from menu</p>
      </div> :
      <div>
        <Model
          model={this.state.tree.find(model => model.name === this.state.menu)}
          requestHeaders={this.state.headers} />
      </div>
    return <MuiThemeProvider theme={this.state.theme}>
      <LeftDrawer
        showAppBar={this.state.showAppBar}
        selected={this.state.menu}
        onItemMenuClick={this.handleItemMenuClick}
        title={this.state.menu ? this.state.menu : 'Configuration'}
        menuItems={this.state.tree.map(model => model.name)}
        content={content} />
    </MuiThemeProvider>
  }
}

export default App;
